.card__compact {
    display: flex;
    height: 7rem !important;
    border-radius: 0.7rem;
    color: white;
    padding: 1rem;
    position: relative;
    cursor: pointer;
    gap: 10px;
    justify-content: space-between;
}

.card__compact:hover {
    box-shadow: none !important;
}
  
.card__radial-bar {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 1rem;
}

.CircularProgressbar {
    width: 4rem !important;
    overflow: visible;
}

.CircularProgressbar-path {
    stroke: white !important;
    stroke-width: 12px !important;
    filter: drop-shadow(2px 4px 6px white);
}

.CircularProgressbar-trail {
    display: none;
}
    
.CircularProgressbar-text {
    fill: white !important;
}
    
.card__title-compact {
    font-size: 17px;
    font-weight: bold;
}

.card__detail {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
}
    
.card__span-compact:nth-child(2) {
    font-size: 22px;
    font-weight: bold;
}

.card__span-compact:nth-child(3) {
    font-size: 12px;
}
    
.card__expanded {
    position: absolute;
    width: 60%;
    height: 70vh;
    z-index: 9;
    left: 13rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 1rem;
}

.card__title-expanded:nth-of-type(1) {
    color: white;
    font-size: 26px;
    font-weight: bold;
    text-shadow: 0px 0px 15px white;
}

.card__title-expanded:nth-of-type(2) {
    color: rgb(236, 236, 236);
    font-size: 15px;
}
    
.card__chart-container-expanded {
    width: 70%;
}
    
@media (max-width: 1200px) {
    .card__expanded {
        top: 2rem;
        height: 45vh;
        left: 6rem;
    }
}
    
@media (max-width: 768px) {
    .card__expanded {
        top: 8rem;
        height: 50%;
        left: 25px;
        width: 80%;
    }
}